import { Link as RouterLink, useLocation, useNavigate } from 'react-router-dom';
// @mui
import { Stack, Typography, Link, Button } from '@mui/material';
// layouts
import LoginLayout from '../../layouts/login';
// routes
import { PATH_AUTH } from '../../routes/paths';
//
import AuthWithSocial from './AuthWithSocial';
import AuthRegisterForm from './AuthRegisterForm';
import axios from 'axios';
import { apiBaseURL } from 'src/config-global';
import { useEffect, useState } from 'react';

import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';

import Terms from '../../assets/Terms.pdf';
import Privacy from '../../assets/Privacy.pdf';

// ----------------------------------------------------------------------

export default function Register() {
  const navigate = useNavigate();
  const location = useLocation();
  const queryParams = new URLSearchParams(location.search);
  const token = queryParams.get('token');

  const [creds, setCreds] = useState(null);
  const resolveToken = async (token) => {
    const res = await axios.get(`${apiBaseURL}/email?token=${token}`);
    setCreds(res.data);
  };

  const [flag, setFlag] = useState(false)
  const [company, setCompany] = useState(null)

  useEffect(() => {
    if (token) {
      resolveToken(token);
    }
  }, []);

  const [open, setOpen] = useState(false);

  const [status, setstatus] = useState(1);

  const handleClickOpen = (s) => {
    setstatus(s);
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };

  return (
    <>
      <Dialog
        open={open}
        fullWidth
        maxWidth="lg"
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogContent>
          {
            status === 1 ? (
              <iframe src={Terms} width="100%" height="800" style={{ border: 'none' }}></iframe>
            ) : (
              <iframe src={Privacy} width="100%" height="800" style={{ border: 'none' }}></iframe>
            )
          }
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} autoFocus>
            Agree
          </Button>
        </DialogActions>
      </Dialog>


    <LoginLayout title="Manage the job more effectively with Minimal">
      <Stack spacing={2} sx={{ mb: 5, position: 'relative' }}>
        <Typography variant="h4">Get started absolutely free.</Typography>

        <Stack direction="row" spacing={0.5}>
          <Typography variant="body2"> Already have an account? </Typography>

          <Link component={RouterLink} to={PATH_AUTH.login} variant="subtitle2">
            Sign in
          </Link>
        </Stack>
      </Stack>

      {token ? creds && <AuthRegisterForm creds={creds} /> : <AuthRegisterForm setCompany={setCompany}/> }

      <Typography
        component="div"
        sx={{ color: 'text.secondary', mt: 3, typography: 'caption', textAlign: 'center' }}
      >
        {'By signing up, I agree to '}
        <Link onClick={() => handleClickOpen(1)} underline="always" color="text.primary">
          Terms of Service
        </Link>
        {' and '}
        <Link onClick={() => handleClickOpen(2)} underline="always" color="text.primary">
          Privacy Policy
        </Link>
        .
      </Typography>

      {!creds && <AuthWithSocial flag={company} />}
    </LoginLayout>
    </>
  );
}
